import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import styled from 'styled-components';
import reactLogo from '../../images/react-logo.svg';
import rekitLogo from '../../images/rekit-logo.svg';
import * as actions from './redux/actions';

const HomeDiv = styled.div`
  text-align: center;

  .app-logo {
    animation: app-logo-spin infinite 10s linear;
    height: 80px;
  }

  // .rekit-logo {
  //   position: absolute;
  //   left: 0px;
  //   top: 0px;
  //   opacity: 0.08;
  //   height: 110px;
  // }

  .app-header {
    background-color: #222;
    height: 70px;
    padding: 20px;
    color: white;
    overflow: hidden;
    position: relative;
  }

  @keyframes app-logo-spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }
`;

const ContentDiv = styled.div`
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: center;
  text-align: center;
  margin: 1%;

  .app-intro {
    font-size: large;
    width: 45%;
  }
`;

const PasswordSpan = styled.div`
  display:inline-flex;
  flex-direction: row;
  border: 1px solid #ff1212;
  margin: 0 auto;
  width: 3rem;
  height: 3rem;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  -moz-border-radius: 50%;
  -webkit-border-radius: 50%;
  cursor: pointer;
  margin: 0 0.3rem 0.3rem 0;

  &:hover {
    background: yellow;
    border: 1px solid gray;
  }
`;

export class DefaultPage extends Component {
  static propTypes = {
    home: PropTypes.object.isRequired,
    actions: PropTypes.object.isRequired,
  };

  constructor(props) {
    super(props);
    this.state = {
      answer: Math.floor(Math.random() * Math.floor(99)) + 1,
      chooseNumber: Array.from({ length: 99 }, (v, i) => i + 1),
      gameEnd: false,
      min: 1,
      max: 99
    };
  }

  handleChooseNumber = (guess) => {
    let chk = Math.max(this.state.answer, guess);
    let newChooseNumber = [];
    if (guess === this.state.answer) {
      this.setState({
        ...this.state,
        gameEnd: !this.state.gameEnd
      });
      alert('!');
    } else {
      if (chk === guess) {
        for (let i = this.state.min; i <= chk; i++) {
          newChooseNumber.push(i);
        }
        this.setState({
          ...this.state,
          max: guess,
          chooseNumber: newChooseNumber
        });
      } else {
        for (let i = guess; i <= this.state.max; i++) {
          newChooseNumber.push(i);
        }
        this.setState({
          ...this.state,
          min: guess,
          chooseNumber: newChooseNumber
        });
      }
    }
  }

  resetGame = () => {
    this.setState({
      answer: Math.floor(Math.random() * Math.floor(99)) + 1,
      chooseNumber: Array.from({ length: 99 }, (v, i) => { return i + 1 }),
      gameEnd: !this.state.gameEnd,
      min: 1,
      max: 99,
    });
  }

  render() {
    console.log(this.state);
    return (
      <HomeDiv>
        <header className="app-header">
          <img src={reactLogo} className="app-logo" alt="logo" />
          {/* <img src={rekitLogo} className="rekit-logo" alt="logo" /> */}
        </header>
        <ContentDiv>
          <div className="app-intro">
            <h1></h1>
            {
              this.state.gameEnd ?
                <button onClick={this.resetGame}></button>
                :
                this.state.chooseNumber.map((number, index) =>
                  <PasswordSpan key={index} onClick={() => this.handleChooseNumber(number)}>
                    {number}
                  </PasswordSpan>
                )
            }
          </div>
        </ContentDiv>
      </HomeDiv>
    );
  }
}

/* istanbul ignore next */
function mapStateToProps(state) {
  return {
    home: state.home,
  };
}

/* istanbul ignore next */
function mapDispatchToProps(dispatch) {
  return {
    actions: bindActionCreators({ ...actions }, dispatch),
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(DefaultPage);



